
[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}


a, [ng-click]{ cursor: pointer; }

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}

body {
  background-color: #f0f0f0;
  font-family: SanFranciscoLight !important;
}


body .toolbar-fixed .page-content {
  padding-bottom: 100px;
}

.content {
  margin-bottom: 50px;
}

/* INPUTS */
.crumbs-form-control {
  height: 34px !important;
  border-radius: 0px !important;
  margin: 0px !important;
  margin-bottom: 15px !important;
  width: 100% !important;
  padding: 2px 5px !important;
    border-top: 0px !important;
    border-left: 0px !important;
    border-right: 0px !important;
    border-bottom: 1px solid #ddd !important;
    box-shadow: none !important;
}

.ui-select-toggle, .ui-select-multiple {
    border-top: 0px !important;
    border-left: 0px !important;
    border-right: 0px !important;
    border-bottom: 1px solid #ddd !important;
    box-shadow: none !important;
}

.crumbs-form-control label {
  padding-top: 7px !important;
}


  /* ==========================================================================
    MODALS
     ========================================================================== */
.modal-content {
  border-radius: 0px !important;
}
.crumbs-cursor {
  cursor: pointer;
  color: gainsboro;
}
.btn-crumbs-cancel {
  background-color: red !important;
  color:white !important;
}
.btn-crumbs-save {
  background-color: green !important;
  color:white !important;
}
.btn-crumbs-goto {
  background-color: lightgreen !important;
  color:white !important;
}
.btn-crumbs-disable {
  background-color: orange !important;
  color:white !important;
}
.btn-crumbs-edit {
  background-color: blue !important;
  color:white !important;
}
.btn-crumbs-link {
  background-color: #58c5e9 !important;
  color:white !important;
}
.btn-crumbs-other {
  background-color: gray !important;
  color:white !important;
}

.crumbs-table > tbody > tr > td, .crumbs-table > thead > tr > td {
  text-align: left !important;
  vertical-align: middle !important;
}

.btn-crumbs {
  height: 55px !important;
  border-radius: 0 !important;
}

.ui-select-toggle {
  border-radius: 0 !important;
}
.ui-select-container {
  margin-bottom: 15px;
}
.ui-select-container > input {
  width: 100% !important;
  margin: 0 !important;
  border-radius: 0 !important;
  background-color: white !important;
  border: 1px solid rgba(0, 0, 0, 0.2) !important;
}

.ui-select-multiple > div > input {
  padding: 2px !important;
  border-radius: 0 !important;
  text-align:left !important;
}
.form-control {
  border-radius: 0 !important;
}
.dropdown-menu {
  border-radius: 0 !important;
}
.brn-run-campaign > btn {
  padding-left: 0px;
}
@media (min-width: 767px) {
  .contacts-details {
    display: none;
  }
  .brn-run-campaign {
    margin-top: 32px !important;
  }
}
@media (max-width: 767px) {
  .brn-run-campaign {
    margin-top: 0px !important;
    margin-bottom: 15px !important;
  }
}

.page-title {
    margin-top: 55px;
}

.numberCircle {
    border-radius: 50% !important;
    width: 36px !important;
    height: 36px !important;
    background: #fff !important;
    border: 2px solid #666 !important;
    color: #666 !important;
    text-align: center !important;
    font-size: 24px !important;
    float: left !important;
    margin-right: 10px !important;
    padding-top: 5px !important;
}



.tab-nav {
    height: 55px !important;
    padding-top: 5px !important;
}

.button-bar > .button {
    border-radius: 0 !important;
}


.menu.menu-left{
    width: calc(30%) !important;
}
.menu-open .menu-content{
    transform: translate3d(calc(30%), 0px, 0px) !important;
}

.ng-invalid-email {
    border-bottom: 1px solid red !important;
}
